/* MOVEABLE */

@layer components {
    .rCS19atnxs {
        position: fixed;
        width: 0;
        height: 0;
        left: 0;
        top: 0;
        z-index: 600;
        --bounds-color: rgb(217 70 239) !important;
    }
    .rCS19atnxs .moveable-control-box{
        z-index: 0;
    }
    .rCS19atnxs .moveable-line, .rCS19atnxs .moveable-control {
        left: 0;
        top: 0;
        will-change: transform;
    }
    .rCS19atnxs .moveable-control {
        @apply rounded-none border-0 bg-transparent -mt-2 -ml-2 !important;
        position: absolute;
        box-sizing: border-box;
        z-index: 10;
    }

    .rCS19atnxs .moveable-control.moveable-clip-control.moveable-snap-control {
        @apply rounded-sm border-0 bg-amber-500 -mt-1 -ml-1 w-2 h-2 !important;
    }

    .rCS19atnxs .moveable-line.moveable-clip-line {
        @apply bg-amber-500 !important;
        z-index: -1 !important;
    }

    .rCS19atnxs .moveable-line.moveable-direction {
        @apply border-t border-l border-blue-500 bg-transparent !important;
    }

    .rCS19atnxs .moveable-line {
        @apply bg-blue-500 !important;
        position: absolute;
        width: 1px;
        height: 1px;
        background: #4af;
        transform-origin: 0px 0.5px;
    }
    .rCS19atnxs .moveable-line.moveable-dashed {
        box-sizing: border-box;
        background: transparent !important;
    }
    .rCS19atnxs .moveable-line.moveable-dashed.moveable-horizontal {
        border-top: 1px dashed #4af;
    }
    .rCS19atnxs .moveable-line.moveable-dashed.moveable-vertical {
        border-left: 1px dashed #4af;
    }
    .rCS19atnxs .moveable-line.moveable-dashed:before {
        @apply text-fuchsia-500 !important;
        position: absolute;
        content: attr(data-size);
        font-size: 12px;
        font-weight: bold;
    }

    .rCS19atnxs .moveable-size-value.moveable-gap {
        @apply text-white bg-amber-500 rounded-sm px-1 !important;
    }

    .rCS19atnxs .moveable-line.moveable-dashed.moveable-horizontal:before {
        left: 50%;
        transform: translateX(-50%);
        bottom: 5px;
    }
    .rCS19atnxs .moveable-line.moveable-dashed.moveable-vertical:before {
        top: 50%;
        transform: translateY(-50%);
        left: 5px;
    }

    .moveable-bold,
    .moveable-target,
    .moveable-vertical {
        @apply bg-fuchsia-500 !important;
        /* background-color: green !important; */
    }

    .moveable-dashed {
        @apply border-amber-500 bg-transparent !important;
        /* border-color: green !important; */
    }
    
    .rCS19atnxs .moveable-line.moveable-rotation-line .moveable-control {
        border-color: #4af;
        background:#fff;
        cursor: alias;
    }
    .rCS19atnxs .moveable-line.moveable-vertical {
        transform: translateX(-50%);
    }
    .rCS19atnxs .moveable-line.moveable-horizontal {
        transform: translateY(-50%);
    }
    .rCS19atnxs .moveable-line.moveable-vertical.moveable-bold {
        width: 1px !important;
    }
    .rCS19atnxs .moveable-line.moveable-horizontal.moveable-bold {
        height: 1px !important;
    }

    .rCS19atnxs .moveable-line.moveable-guideline.moveable-vertical.moveable-bold {
        @apply bg-fuchsia-500 !important;
    }

    .rCS19atnxs .moveable-line.moveable-guideline.moveable-horizontal.moveable-bold {
        @apply bg-fuchsia-500 !important;
    }

    .rCS19atnxs .moveable-line.moveable-guideline.moveable-gap {
        @apply bg-amber-500 !important;
    }

    /* .rCS19atnxs .moveable-control.moveable-origin{
        @apply bg-blue-500 rounded-full !important;
        background: #fff;
        width: 12px;
        height: 12px;
        margin-top: -6px;
        margin-left: -6px;
        pointer-events: none;
    } */

    .rCS19atnxs .moveable-control.moveable-rotation-control {
        @apply bg-blue-500 border-blue-500 rounded-full !important;
    }

    .rCS19atnxs .moveable-group {
        z-index: -1;
    }
    .rCS19atnxs .moveable-area {
        position: absolute;
    }
    .rCS19atnxs .moveable-area-pieces {
        position: absolute;
        top: 0;
        left: 0;
        display: none;
    }
    .rCS19atnxs .moveable-area.moveable-avoid {
        pointer-events: none;
    }
    .rCS19atnxs .moveable-area.moveable-avoid+.moveable-area-pieces {
        display: block;
    }
    .rCS19atnxs .moveable-area-piece {
        position: absolute;
    }

    .rCS19atnxs .moveable-direction {
        @apply pointer-events-auto !important;
    }

    /* Right */
    .rCS19atnxs .moveable-direction.moveable-e {
        @apply absolute focus:outline-none w-4 -mt-3 h-6 cursor-ew-resize border-r-2 border-blue-500 !important;
    }

    /* Left */
    .rCS19atnxs .moveable-direction.moveable-w {
        @apply absolute focus:outline-none w-4 -mt-3 h-6 cursor-ew-resize border-l-2 border-blue-500 !important;
    }

    /* Bottom */
    .rCS19atnxs .moveable-direction.moveable-s {
        @apply absolute focus:outline-none w-6 -ml-3 h-4 cursor-ns-resize border-b-2 border-blue-500 !important;
    }

    /* Top */
    .rCS19atnxs .moveable-direction.moveable-n {
        @apply absolute focus:outline-none w-6 -ml-3 h-4 cursor-ns-resize border-t-2 border-blue-500 !important;
    }

    /* Top left */
    .rCS19atnxs .moveable-direction.moveable-nw, .rCS19atnxs .moveable-reverse .moveable-direction.moveable-nw {
        @apply absolute focus:outline-none cursor-nwse-resize /* -mt-1 -ml-1 */ w-4 h-4 border-t-2 border-l-2 rounded-tl border-blue-500 !important;
    }

    /* Top right */
    .rCS19atnxs .moveable-direction.moveable-ne, .rCS19atnxs .moveable-reverse .moveable-direction.moveable-ne {
        @apply absolute focus:outline-none cursor-nesw-resize /* -mt-1 -ml-3 */ w-4 h-4 border-t-2 border-r-2 rounded-tr border-blue-500 !important;
    }

    /* Bottom left */
    .rCS19atnxs .moveable-direction.moveable-sw, .rCS19atnxs .moveable-reverse .moveable-direction.moveable-sw {
        @apply absolute focus:outline-none cursor-nesw-resize /* -mt-3 -ml-1 */ w-4 h-4 border-b-2 border-l-2 rounded-bl border-blue-500 !important;
    }

    /* Bottom right */
    .rCS19atnxs .moveable-direction.moveable-se, .rCS19atnxs .moveable-reverse .moveable-direction.moveable-se {
        @apply absolute focus:outline-none cursor-nwse-resize /* -mt-3 -ml-3 */ w-4 h-4 border-b-2 border-r-2 rounded-br border-blue-500 !important;
    }
}


.rCS19atnxs {
	position: absolute;
	width: 1px;
	height: 1px;
	left: 0;
	top: 0;
    z-index: 3000;
    --moveable-color: #4af;
    --zoom: 1;
    --zoompx: 1px;
    will-change: transform;
}
.rCS19atnxs .moveable-control-box {
    z-index: 0;
}
.rCS19atnxs .moveable-line, .rCS19atnxs .moveable-control {
    position: absolute;
	left: 0;
    top: 0;
    will-change: transform;
}
.rCS19atnxs .moveable-control {
	width: 14px;
	height: 14px;
	border-radius: 50%;
	border: 2px solid #fff;
	box-sizing: border-box;
    background: #4af;
    background: var(--moveable-color);
	margin-top: -7px;
    margin-left: -7px;
    border: 2px solid #fff;
    z-index: 10;
}
.rCS19atnxs .moveable-padding {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100px;
    height: 100px;
    transform-origin: 0 0;
}
.rCS19atnxs .moveable-line {
	width: 1px;
    height: 1px;
    background: #4af;
    background: var(--moveable-color);
	transform-origin: 0px 50%;
}
.rCS19atnxs .moveable-line.moveable-dashed {
    box-sizing: border-box;
    background: transparent;
}
.rCS19atnxs .moveable-line.moveable-dashed.moveable-horizontal {
    border-top: 1px dashed #4af;
    border-top-color: #4af;
    border-top-color: var(--moveable-color);
}
.rCS19atnxs .moveable-line.moveable-dashed.moveable-vertical {
    border-left: 1px dashed #4af;
    border-left-color: #4af;
    border-left-color: var(--moveable-color);
}
.rCS19atnxs .moveable-line.moveable-vertical {
    transform: translateX(-50%);
}
.rCS19atnxs .moveable-line.moveable-horizontal {
    transform: translateY(-50%);
}
.rCS19atnxs .moveable-line.moveable-vertical.moveable-bold {
    width: 2px;
}
.rCS19atnxs .moveable-line.moveable-horizontal.moveable-bold {
    height: 2px;
}

.rCS19atnxs .moveable-control.moveable-origin {
	border-color: #f55;
	background: #fff;
	width: 12px;
	height: 12px;
	margin-top: -6px;
    margin-left: -6px;
	pointer-events: none;
}

